.sidenav {
  height: 100%;
  width: 5%;
  position: fixed;
  z-index: 3;
  top: 0;
  left: 95%;
  background-color: #fff;
  border-left: solid #BDBDBDBD 1px;

	&>a.aboutButton{
		margin-top: 50%;
	}
	&>a {
		margin-bottom: 100px;
		margin-left: -25%;
		margin-right: -50%;
		text-decoration: none;
	  font-size: 20px;
	  color: #8E8E8E;
	  display: block;
	  transform: rotate(-90deg);
  	transition: all 0ms;
  	-webkit-transition: all 0ms;
	  width: 100px;
	}
	&>a:hover {
		color:#020202;		
		}
	&>a.active {
  	color: #FF9944;
	}
}

@keyframes animateleft{
	from{
		padding-right: -50%;
	}
	to{	
		padding-right:83%;
	}
}

