.skills-section{
	text-align: center;
	color: #393939;	
	letter-spacing: .2rem;
	h2{
	  font-size: 1.5rem;	
		border-bottom: solid #FF9944 1vh;
		width: 70%;
	  display: block;
	  height:22px;
	}
	.skills {
		&>h2{
			width: 30%;
			margin-left: 35%;
		}
		width: 35%;
		padding: 0px;
    display: block;
	  font-size: 1.0rem;
	}
	.software{
		position: absolute;
		top:0;
		left:40%;
		width: 35%;
	 	font-size: 1.0rem;
		&>h2{
			margin-left: 15%;
		}
	}
	.spelling{
		position: absolute;
		top:40%;
		left:40%;
		width:35%;
	 	font-size: 1.0rem;
		&>h2{
			margin-left: 15%;
		}
	}
	.quote{
		font-style: italic;
		position: absolute;
		margin-top:28vh;
		margin-left:2vh;
		letter-spacing: .1rem;
	}
}