.welcome{
	.welcome-image{
		filter: grayscale(100%);
		opacity: 0.5;
		img{
		  display: block;
		  margin:auto;
			margin-top: 5vh;
			border-radius: 50%;
			max-width: 100%;
			height: 80vh;
		}
	}
	.welcome-text{
		position:absolute;
		z-index: 1;
		opacity: 0.8;
		top: 2%;
		left: 2%;
	  display: block;
	  font-size: 1.8rem;
		letter-spacing: .2rem;
		color: #393939;	
	}
	.welcome-name{
		color: #393939;
		position:absolute;
		z-index: 1;
		margin-left:22%;
		margin-top: 20%;
	  font-size: 3.8rem;
		letter-spacing: .2rem;	
		border-bottom: solid #FF9944 1.8vh;
	  display: block;
	  height:50px;
	}
	.lastname{
  	margin-left:25%;
		margin-top:25%;
	}
	.welcome-job{
		position:absolute;
		z-index: 1;
		opacity: 0.8;
		top: 85%;
		left: 65%;
	  display: block;
	  font-size: 1.8rem;
		letter-spacing: .2rem;
		color: #393939;	
	}
}