$orange: #FF9944;	

.degrees{
	text-align: center;
	color: #393939;	
	letter-spacing: .2rem;
	&>h2{
	  font-size: 2rem;	
		border-bottom: solid #FF9944 1vh;
		width: 20%;
	  display: block;
	  height:29px;
		margin: 5% 0% 5% 40%;
	}
	&>h4{
		position: absolute;
		&>span{
			z-index: 2;
			padding-top: 45px;
			background-color: #fff;
    	border: 1px solid #eee;
    	border-radius: 100%;
    	text-align: center;
    	width:120px;
    	height: 75px;
			display:block;
			&.first{
    		margin-top: -66%;
				margin-left: 800%;
    	}
    	&.second{
    		margin-top: -66%;
				margin-left: 500%;
	    }
	    &.third{
    		margin-top: -66%;
    		margin-left: 200%;
		  }
  	}
		&.first{
			margin-top: 6%;
			margin-left: 70%;
		}
		&.second{
			margin-top: -8%;
			margin-left: 43%;
		}
	  &.third{
			margin-top: 6%;
			margin-left: 15%;
		}
  }
}
.timeline{
	height:2px;
	margin-top: 15%;
	margin-left: 20%;
	width: 60%;
	background-color: #eee;
}