.social{
	position: absolute;
	display: block;
	width: 30%;
	opacity: 0.8;
	letter-spacing: .2rem;
	color: #393939;	
	&>span{
		padding-right: 13%;
	}
	.media{
		padding-right: 3%;
		text-decoration: none;
		color:#393939;
	}
	.media:hover{
		&>span{
			position: relative;
			-moz-animation: bounce 0.5s infinite linear;
	  	-o-animation: bounce 0.5s infinite linear;
	  	-webkit-animation: bounce 0.5s infinite linear;
	  	animation: bounce 0.5s infinite linear;
		}
	}	
}

@keyframes bounce {
    0% { top: 0; }
    50% { top: -0.2em; }
    70% { top: -0.3em; }
    100% { top: 0; }
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}