$orange: #FF9944;	
//#FF7400;

.about{
	width: 90%;
	.about-image{
		filter: grayscale(100%);
		float: left;
		padding-left:1%;
		padding-top:2.5%;
		img{
			border-radius: 2%;
			max-width: 100%;
			height: 90vh;
		}
	}
	.about-title{
		color: #000;
		position:absolute;
		margin-left: 40%;
		font-size: 50px;
		border-bottom: solid $orange 1.5vh;
	  display: block;
	  float:left;
	  text-align: left;
	  padding-left::2%;		
	  width: 55%;
	  height:92px;
	}
	.about-desc{
		padding-left: 53%;
		text-align: justify;
		font-size: 20px;
		padding-top: 10%;
		& span{
			color:$orange;
			font-weight: bold;
		}
	}
	.about-button{
		background-color:$orange;
		position: absolute;
		text-align: left;
		padding: 1vh;
		margin-left: 40%;
		margin-top: 10%;
		width: 18%;
		&>a{
			color:#000;
			text-decoration: none;
		}
	}
}